.aft-btns{
    display: flex;
    padding-left: 31%;
}

.read-btn{
    margin: 2%;
}

.video-btn{
    margin: 2%;
}

.btn-link-read{
    color:#c1d6c2;
    text-decoration: none;
}

.btn-link-read:hover{
    color: whitesmoke;
}

.btn-link-read:visited{
    text-decoration: none;
}

.btn-link-video{
    color:#487548;
    text-decoration: none;
}

.btn-link-video:hover{
    color: white;
}

.btn-link-video:visited{
    text-decoration: none;
    color: #487548;
}

footer {
    background-color: black;
    color: white;
    padding: 0.5%;
    text-align: center;
    font-size: large;
  }



  @media (max-width: 900px) {
    .rates{
        display: block;
        width: 100%;
    }
    .msp{
        width: 65%;
        padding: 3%;
        padding-left: 10%;
        padding-right: 0%;
    }
    .msp-head{
        padding-left: 45%;
        padding-right: 0%;
    }
    .msp-img{
        padding-left: 25%;
    }
    .msp-info{
        padding-left: 40%;
        padding-right: 0%;
    }
    .msp-btn{
        margin-left: 58.5%;
    }
    .cr{
        width: 80%;
        padding: 3%;
        padding-left: 25%;
    }
    .cr-btn{
        margin-left: 31%;
    }

    .technique{
        display: block;
    }
    .perma-img{
        padding-left: 2.5%;
    }
    .perma-content{
        padding-top: 0.5%;
        padding-right: 0%;
        padding-left: 20%;
    }
    .pond-content{
        width: 80%;
        padding-right: 0%;
        padding-left: 20%;
    }
    .pond-img{
        width: 70%;
        padding-top: 1%;
    }
    .agro-img{
        padding-right: 5%;
        padding-left: 5%;
    }
    .agro-content{
        padding-left: 18%;
        padding-right: 0%;
    }
    .biodynamic-content{
        width: 80%;
        padding-right: 0%;
        padding-left: 18%;
    }
  }
